.container {
  max-width: 1240px;
  padding: 0 20px;
  margin: 0 auto;
}

@media (max-width: 992px) {
  .container {
    max-width: 576px;
  }
}
.modals {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  overflow: auto;
  padding: 65px 40px 40px;
  background: #fff;
  transform: scale(0);
  transition: 0.3s ease;
}

.review__modal {
  transform: scale(0);
  flex-direction: column;
}

.cross {
  position: absolute;
  top: 10px;
  right: 10px;
  height: 43px;
  width: 43px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.review__modal-author {
  font-family: "OpenSans";
  font-weight: 400;
  font-size: 18px;
  line-height: 1.17;
  color: #d5b438;
  margin-bottom: 7px;
}

.review__modal-expert-city {
  font-family: "OpenSans";
  font-weight: 400;
  font-size: 16px;
  line-height: 1.45;
  color: #122453;
  opacity: 0.7;
  margin-bottom: 40px;
}

.review__modal-text {
  font-family: "OpenSans";
  font-weight: 400;
  font-size: 18px;
  line-height: 1.45;
  color: #122453;
}

.modals__active {
  transform: scale(1);
  transition: 0.3s ease;
}

.success__modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  overflow: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.5);
  transform: scale(0);
  transition: 0.3s ease;
}

.success__modal-wrapper {
  background: #fff;
  padding: 40px 40px;
  max-width: 400px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  border-radius: 8px;
  position: relative;
}

.seccess__modal-img {
  max-width: 48px;
  max-height: 48px;
}

.success__modal-text {
  font-family: "OpenSans";
  font-weight: 400;
  font-size: 18px;
  line-height: 28px;
  color: #122453;
  text-align: center;
}

.success__modal-active {
  transition: 0.3s ease;
  transform: scale(1);
}

.slick-list {
  overflow: hidden;
}

.slick-track {
  display: flex;
}

.about__slider-item img {
  max-height: 950px;
  min-height: 650px;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

header {
  padding-top: 20px;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  gap: 30px;
  flex-wrap: wrap;
}

.header__logo {
  display: block;
  max-width: 250px;
  flex: 1 1 auto;
}
.header__logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
}

.header__rating {
  display: flex;
  position: relative;
  align-self: stretch;
  flex: 1 1 auto;
  justify-content: center;
}
.header__rating p {
  padding-bottom: 10px;
  max-width: 87px;
  align-self: flex-end;
  font-family: "OpenSans";
  font-weight: 400;
  font-size: 18px;
  line-height: 120%;
  color: #fff;
}

.header__bookmark-img {
  position: relative;
  top: -20px;
  left: 0;
  width: 80px;
  height: 104px;
}
.header__bookmark-img p {
  padding-left: initial;
  max-width: initial;
  position: absolute;
  left: 28px;
  top: 40px;
  font-family: "OpenSans";
  font-weight: 400;
  font-size: 18px;
  line-height: 120%;
  color: #122453;
}
.header__bookmark-img img {
  width: 100%;
  height: 100%;
}

.header__social {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.header__social a {
  display: none;
}

.header__social-text {
  font-family: "OpenSans";
  font-weight: 400;
  font-size: 18px;
  line-height: 120%;
  color: #fff;
}

.animationCircle {
  display: flex;
  align-items: center;
  gap: 15px;
}
.animationCircle::before {
  content: "";
  display: block;
  width: 15px;
  height: 15px;
  background: #d5b438;
  border-radius: 50%;
  animation-name: circle;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes circle {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
.header__social-list {
  display: flex;
  align-items: center;
  gap: 5px;
  justify-content: center;
}

.header__social-list-item {
  max-width: 35px;
  max-height: 35px;
  cursor: pointer;
}
.header__social-list-item a {
  display: block;
}
.header__social-list-item a img {
  width: 100%;
  height: 100%;
}

.header__callback-text-wrap {
  display: flex;
  align-items: center;
  gap: 5px;
}

.header__callback {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}
.header__callback a {
  font-family: "OpenSans";
  font-weight: 700;
  font-size: 30px;
  line-height: 120%;
  color: #fff;
}

.header__callback-text {
  font-family: "OpenSans";
  font-weight: 400;
  font-size: 18px;
  line-height: 120%;
  color: #fff;
}

.header__callback-btn {
  font-family: "OpenSans";
  font-weight: 700;
  font-size: 18px;
  line-height: 120%;
  color: #fff;
  background: none;
  transition: 0.3s ease;
  cursor: pointer;
  animation: smoothly 0.7s ease;
  animation-iteration-count: 1;
}
.header__callback-btn:hover {
  transition: 0.3s ease;
  color: #d5b438;
}

.header__social-callback-wrap {
  display: flex;
  flex: 1 1 auto;
  justify-content: space-between;
}

@media (max-width: 992px) {
  header {
    padding-top: 100px;
  }
  .header__wrapper {
    gap: 5px;
  }
  .animationCircle::before {
    display: none;
  }
  .header__social-list-item {
    max-width: 25px;
    max-height: 25px;
  }
  .header__social-callback-wrap {
    flex-direction: column;
    gap: 5px;
    flex: initial;
  }
  .header__callback-text-wrap {
    flex-direction: column;
  }
  .header__logo {
    display: none;
  }
  .header__social-text {
    font-size: 16px;
  }
  .header__callback-text {
    font-size: 16px;
  }
  .header__callback a {
    font-size: 16px;
  }
  .header__rating {
    top: -10px;
    flex: initial;
  }
  .header__rating p {
    font-size: 16px;
  }
  .header__bookmark-img {
    width: 65px;
    top: initial;
  }
  .header__bookmark-img p {
    left: 22px;
    font-size: 16px;
  }
  .header__callback-btn {
    font-size: 16px;
  }
}
.about__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
}

.main__nav {
  margin-top: 20px;
}

.main__nav-logo {
  display: none;
  max-width: 130px;
}
.main__nav-logo img {
  width: 100%;
  height: 100%;
}

.main__nav-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  flex-wrap: wrap;
  animation: smoothly 0.7s ease;
  animation-iteration-count: 1;
}

@keyframes smoothly {
  0% {
    transform: translateY(1000%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.main__nav-list-item a {
  font-family: "OpenSans";
  font-weight: 400;
  font-size: 18px;
  line-height: 1.55;
  color: #fff;
  transition: 0.3s ease;
}
.main__nav-list-item a:hover {
  transition: 0.3s ease;
  color: #d5b438;
}

.main__nav-list-social {
  display: none;
}

.main__nav-phone {
  display: none;
}

@media (max-width: 992px) {
  .main__nav {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    padding: 20px;
    background: #122453;
    z-index: 10;
  }
  .main__nav-list {
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .main__nav-list-wrapper {
    position: fixed;
    top: 89px;
    left: 0;
    background: #122453;
    width: 100%;
    padding-bottom: 45px;
    transform: scale(0);
    transition: 0.3s ease;
  }
  .main__nav-list-item a {
    font-size: 16px;
  }
  .main__nav-logo {
    display: block;
  }
  .main__nav-burger {
    display: block;
  }
  .main__nav-list-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .main__nav-list-social {
    margin-top: 50px;
    display: flex;
    align-items: center;
    gap: 10px;
  }
  .main__nav-phone {
    margin-top: 20px;
    font-family: "OpenSans";
    font-weight: 600;
    font-size: 14px;
    line-height: 1.55;
    color: #d5b438;
    border: 2px solid #d5b438;
    border-radius: 30px;
    padding: 5px 10px;
    width: 100%;
    max-width: 195px;
    text-align: center;
    display: inline;
  }
}
.main__nav-active {
  transform: scale(1);
  transition: 0.3s ease;
}

.main__nav-burger {
  display: none;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 28px;
  height: 20px;
}
.main__nav-burger span {
  display: block;
  width: 100%;
  height: 2px;
  border-radius: 8px;
  background: #fff;
  transition: 0.3s ease;
}
.main__nav-burger::before, .main__nav-burger::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  border-radius: 8px;
  background: #fff;
  position: absolute;
  left: 0;
  transition: 0.3s ease;
}
.main__nav-burger::before {
  top: 0;
}
.main__nav-burger::after {
  bottom: 0;
}

.main__nav-burger-active span {
  opacity: 0;
  transition: 0.3s ease;
}
.main__nav-burger-active::before {
  transform: rotate(45deg);
  transition: 0.3s ease;
  top: 10px;
}
.main__nav-burger-active::after {
  transition: 0.3s ease;
  transform: rotate(-45deg);
  bottom: 8px;
}

@media (max-width: 992px) {
  .main__nav-burger {
    display: flex;
  }
}
.about {
  position: relative;
}

.main__title {
  margin-top: 160px;
  font-family: "OpenSans";
  font-weight: 600;
  font-size: 50px;
  line-height: 65px;
  color: #fff;
  animation: smoothly 0.7s ease;
  animation-iteration-count: 1;
}

.main__text {
  font-family: "OpenSans";
  font-weight: 600;
  font-size: 18px;
  line-height: 1.55;
  color: #fff;
  margin-top: 30px;
  font-style: italic;
  animation: smoothly 0.7s ease;
  animation-iteration-count: 1;
}

.main__btn {
  margin-top: 160px;
  display: inline-block;
  background: none;
  font-family: "OpenSans";
  font-weight: 400;
  font-size: 18px;
  line-height: 1.55;
  color: #d5b438;
  border: 2px solid #d5b438;
  border-radius: 30px;
