}

@keyframes consultationBtn {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.1, 1.1);
  }
  100% {
    transform: scale(1, 1);
  }
}
@media (max-width: 992px) {
  .main__title {
    margin-top: 60px;
    font-size: 40px;
  }
  .main__text {
    margin-top: 15px;
    font-size: 14px;
  }
  .main__btn {
    font-size: 14px;
    margin-top: 30px;
  }
}
